import React from "react";
import Container from "react-bootstrap/Container";
import MobileBanner from "../images/mobilebanner.png";
import { Row, Col } from "react-bootstrap";

function Banner() {
  return (
    <Container fluid="md">
      <Row className="mt-4">
        <Col md="6">
          <h1>
            Providing communities with group financing to grow small scale
            business, Livestock, Poultry Farming or any agribusiness
          </h1>
        </Col>
        <Col>
          {" "}
          <img src={MobileBanner} alt="logo" className="mobilebanner" />
        </Col>
      </Row>
    </Container>
  );
}

export default Banner;
