import React from "react";
import { Card, CardGroup, Button } from "react-bootstrap";
import womengroups from "../images/womengroups.jpg";
import jengabiashara from "../images/jengabiashara.jpg";
import okoajahazi from "../images/okoajahazi.jpg";

function ProductsHomepage() {
  return (
    <div className="m-4">
      <CardGroup>
        <Card>
          <Card.Img variant="top" src={jengabiashara} />
          <Card.Body>
            <Card.Title>Jenga Biashara</Card.Title>
            <Card.Text>
              Get fast access to a business cash advance Your business qualifies
              for a cash advance after consistently transacting on an account
              acquired through G-Loan Savings for 60 days. Every business is
              unique and your transaction volumes and patterns will determine
              your cash advance limit.
            </Card.Text>
            <Button variant="warning">Learn More</Button>
          </Card.Body>
        </Card>
        <Card>
          <Card.Img variant="top" src={womengroups} />
          <Card.Body>
            <Card.Title>Save</Card.Title>
            <Card.Text>
              This card has supporting text below as a natural lead-in to
              additional content.{" "}
            </Card.Text>
            <Button variant="secondary">Learn More</Button>
          </Card.Body>
        </Card>
        <Card>
          <Card.Img variant="top" src={okoajahazi} />
          <Card.Body>
            <Card.Title>Okoa Jahazi</Card.Title>
            <Card.Text>
              This is a wider card with supporting text below as a natural
              lead-in to additional content. This card has even longer content
              than the first to show that equal height action.
            </Card.Text>
            <Button variant="warning">Learn More</Button>
          </Card.Body>
        </Card>
      </CardGroup>
    </div>
  );
}

export default ProductsHomepage;
